import React from "react";
import "../App.css"; // Import the CSS file you created above

const AstromonCard = ({ astromon }) => {
  return (
    <div className="astromon-card">
      {/* Display the icon with a half-fade effect */}
      <img
        className="astromon-image"
        src={`/path-to-icons/${astromon.icon_name}.png`} // Replace with the correct path to your icons
        alt={astromon.name}
      />

      {/* Display the stats */}
      <ul className="astromon-stats">
        <li className="stat-item">HP: {astromon.hp}</li>
        <li className="stat-item">ATK: {astromon.atk}</li>
        <li className="stat-item">DEF: {astromon.def}</li>
        <li className="stat-item">REC: {astromon.rec}</li>
      </ul>
    </div>
  );
};

// Example parent component that maps through astromon data
const AstromonList = ({ astromons }) => {
  return (
    <div className="card-container">
      {astromons.map((astromon) => (
        <AstromonCard key={astromon.slug} astromon={astromon} />
      ))}
    </div>
  );
};

export default AstromonCard;
