import React, { useState, useEffect } from "react";

const AstromonBaseCard = ({ onSelect }) => {
  const [astromons, setAstromons] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [filteredResults, setFilteredResults] = useState([]);
  const [selectedName, setSelectedName] = useState("");

  useEffect(() => {
    // Fetch the astromon data from the JSON file
    const fetchAstromons = async () => {
      try {
        const response = await fetch("data/astromon-families.json");
        const data = await response.json();
        setAstromons(data);
      } catch (error) {
        console.error("Error loading astromon data:", error);
      }
    };

    fetchAstromons();
  }, []);

  useEffect(() => {
    // Filter results based on search query
    const results = astromons
      .flat() // Flatten the array of arrays
      .filter((name) => name.toLowerCase().includes(searchQuery.toLowerCase()))
      .slice(0, 5); // Limit to first 5 results
    setFilteredResults(results);
  }, [searchQuery, astromons]);

  const handleSelect = (slug) => {
    if (onSelect) {
      onSelect(slug); // Call the parent function with the selected slug
      setSelectedName(slug); // Update selected name for display
    }
  };

  return (
    <div
      className="tool-card"
      style={{
        position: "relative",
        padding: "20px",
        borderRadius: "10px",
        background:
          "url('/path-to-background-icon.png') no-repeat center center/cover",
      }}
    >
      {/* Search box */}
      <input
        type="text"
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
        placeholder="Search Astromon"
        style={{
          width: "100%",
          padding: "10px",
          marginBottom: "20px",
          borderRadius: "5px",
        }}
      />

      {/* Selected Name with Element Icon */}
      {selectedName && (
        <div className="selected-name">
          <img
            src={`/path-to-element-icon/${selectedName}.png`} // Path to the selected element icon
            alt="Element Icon"
            onClick={() => console.log("Change Element Functionality")}
            style={{
              cursor: "pointer",
              width: "30px",
              height: "30px",
              marginRight: "10px",
            }}
          />
          <span>{selectedName}</span>
        </div>
      )}

      {/* Evolution Icons (Placeholder for icons, replace with actual paths) */}
      <div
        className="evolution-icons"
        style={{
          display: "flex",
          justifyContent: "space-between",
          margin: "20px 0",
        }}
      >
        {["evolution1", "evolution2", "evolution3", "evolution4"].map(
          (evolution, index) => (
            <img
              key={index}
              src={`/path-to-evolution-icons/${evolution}.png`} // Path to evolution icons
              alt={`Evolution ${index + 1}`}
              style={{ width: "50px", height: "50px", cursor: "pointer" }}
              onClick={() => console.log(`Select Evolution ${index + 1}`)} // Replace with actual functionality
            />
          )
        )}
      </div>

      {/* Selected Stats */}
      <div
        className="stats-grid"
        style={{
          display: "grid",
          gridTemplateColumns: "repeat(2, 1fr)",
          gap: "10px",
        }}
      >
        <div>HP: 100</div> {/* Replace with actual HP */}
        <div>ATK: 50</div> {/* Replace with actual ATK */}
        <div>DEF: 30</div> {/* Replace with actual DEF */}
        <div>REC: 20</div> {/* Replace with actual REC */}
      </div>

      {/* Display filtered results */}
      {filteredResults.length > 0 && (
        <ul style={{ listStyleType: "none", padding: "0" }}>
          {filteredResults.map((name, index) => (
            <li
              key={index}
              onClick={() => handleSelect(name)} // Handle the selection
              style={{
                cursor: "pointer",
                padding: "5px",
                borderBottom: "1px solid #ccc",
              }}
            >
              {name}
            </li>
          ))}
        </ul>
      )}

      {/* Skill Boxes */}
      <div
        className="skill-boxes"
        style={{
          display: "flex",
          justifyContent: "space-between",
          marginTop: "20px",
        }}
      >
        {Array.from({ length: 4 }).map((_, index) => (
          <div
            key={index}
            style={{
              width: "50px",
              height: "50px",
              border: "1px solid #000",
              borderRadius: "5px",
              textAlign: "center",
              lineHeight: "50px",
            }}
            onClick={() => console.log(`Select Skill ${index + 1}`)} // Replace with actual functionality
          >
            Skill {index + 1}
          </div>
        ))}
      </div>
    </div>
  );
};

export default AstromonBaseCard;
