import React, { useState, useEffect } from "react";

// Function to dynamically import all card components from the buildcards folder
const importAllCards = () => {
  const context = require.context("../components/buildcards", true, /\.js$/);
  const cards = context.keys().map((key) => {
    const componentName = key.replace("./", "").replace(".js", "");
    return { name: componentName, component: context(key).default };
  });
  return cards;
};

const Builder = () => {
  const [cards, setCards] = useState([]); // Array of added cards
  const [availableCards, setAvailableCards] = useState([]); // List of available cards
  const [showCardOptions, setShowCardOptions] = useState(false); // Controls card selection popup

  useEffect(() => {
    // Load all card components from the buildcards folder dynamically
    const loadedCards = importAllCards();
    setAvailableCards(loadedCards);
  }, []);

  // Function to handle adding a new card
  const addCard = (CardComponent) => {
    setCards([...cards, <CardComponent key={cards.length} />]);
    setShowCardOptions(false); // Hide card options after adding
  };

  // Determine the list of cards available based on current selection
  const getFilteredAvailableCards = () => {
    return availableCards.filter(
      (card) =>
        card.name !== "BlankCard" &&
        !cards.some((existingCard) => existingCard.type === card.component)
    );
  };

  return (
    <div className="tool-page">
      <h2>Builder Tool</h2>

      {/* Render current cards */}
      <div className="card-area">
        {cards.map((CardComponent, index) => (
          <div key={index} className="card-wrapper">
            {CardComponent}
          </div>
        ))}

        {/* Add the + blank card */}
        <div className="card-wrapper">
          {(() => {
            const ToolCardComponent = availableCards.find(
              (card) => card.name === "BlankCard"
            )?.component;
            return ToolCardComponent ? (
              <ToolCardComponent onClick={() => setShowCardOptions(true)} />
            ) : null;
          })()}
        </div>
      </div>

      {/* Show available card options when + is clicked */}
      {showCardOptions && (
        <div className="card-options">
          <h3>Select a Card to Add:</h3>
          {getFilteredAvailableCards().map((card, index) => (
            <button key={index} onClick={() => addCard(card.component)}>
              {card.name}
            </button>
          ))}
        </div>
      )}
    </div>
  );
};

export default Builder;
